:root {
  --active-color: #00acdc;
}

body {
  text-transform: uppercase;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  height: 40px;
  margin-left: 50px;
  text-decoration: none;
  display: block;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

li:last-child {
  margin-right: 0;
}
